@import "../../styles/global";

.wrap {
  width: 100%;
  background-color: $dark;
  height: 100px;
  padding: 0 25px 0 45px;
  display: grid;
  grid-template-columns: 152px auto 133px;
  p {
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: #ffffff;
    padding-left: 83px;
    padding-top: 18px;
  }
}
