@import "../../styles/global";
.wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  padding: 30px 0;
  p {
    font-weight: 700;
    font-size: 28px;
    line-height: 33px;
    span {
      color: $primary;
    }
  }
  .wrap_links {
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: black;
    a {
      transition: all 300ms ease;
    }
    a:hover,
    a:active {
      color: $primary;
    }
    span {
      margin: 10px;
    }
  }
}
